export default {
  data () {
    return {
      count: 0,
      renew: 0,
      list: [],
      start: '2017-10',
      end: '',
      startDate: new Date('2017-10-01'),
      endDate: new Date(),
      s_year: 2017,
      s_month: 10,
      e_year: '',
      e_month: '',
      cur_start: '', // 进入详情需要的
      cur_end: '' // 进入详情需要的
    }
  },
  onLoad () {
    uni.showLoading({
        title: '加载中',
    	mask:"true"
    });
	this.e_year = this.endDate.getFullYear()
	this.e_month = this.endDate.getMonth() + 1
	this.end = this.e_year + '-' + this.e_month
  	this.getData(0)
  },
  methods: {
	  gogo(item){
		  uni.navigateTo({
		  	url:'../../pagesThree/HistoryDetail/HistoryDetail?start='+item.startDate + '&end=' +item.endDate
		  })
	  },
    getData (e) {
      this.$api.month_open_renew({
        start: this.start,
        end: this.end
      }).then(res => {
        uni.hideLoading();
        if (res.data.code === '200') {
          this.count = res.data.data.search_res.open
          this.renew = res.data.data.search_res.renew
          if (e === 0) {
            this.list = res.data.data.list
          }
        }
      })
    },
	pic1(e){
		// console.log(e.detail.value.split('-')[0])
		let year = e.detail.value.split('-')[0]
		let month = e.detail.value.split('-')[1]
		this.s_year = year
		this.s_month = month
		this.start = e.detail.value
		if (this.s_year > this.e_year) {
		  this.count = 0
		  this.renew = 0
		  uni.showToast({
		      title: '开始年份不能大于结束年份',
		      mask: "true",
		  	icon:'none'
		  });
		} else if (this.s_year < this.e_year) {
		  this.count = 0
		  this.renew = 0
		  uni.showLoading({
		      title: '加载中',
		  	mask:"true"
		  });
		  this.getData(1)
		} else if (this.s_year === this.e_year) {
		  if (this.s_month <= this.e_month) {
		    this.count = 0
		    this.renew = 0
		    uni.showLoading({
		        title: '加载中',
		    	mask:"true"
		    });
		    this.getData(1)
		  } else {
		    this.count = 0
		    this.renew = 0
			uni.showToast({
			    title: '开始月份不能大于结束月份',
			    mask: "true",
				icon:'none'
			});
		  }
		}
	},
	pic2(e){
		// console.log(e.detail.value.split('-')[0])
		let year = e.detail.value.split('-')[0]
		let month = e.detail.value.split('-')[1]
		this.e_year = year
		this.e_month = month
		this.end = e.detail.value
		if (this.s_year > this.e_year) {
		  this.count = 0
		  this.renew = 0
		  uni.showToast({
		      title: '结束年份不能小于开始年份',
		      mask: "true",
		  	icon:'none'
		  });
		} else if (this.s_year < this.e_year) {
		  this.count = 0
		  this.renew = 0
		  // this.list = []
		  uni.showLoading({
		      title: '加载中',
		  	mask:"true"
		  });
		  this.getData()
		} else if (this.s_year === this.e_year) {
		  if (this.s_month <= this.e_month) {
		    this.count = 0
		    this.renew = 0
		    // this.list = []
		    uni.showLoading({
		        title: '加载中',
		    	mask:"true"
		    });
		    this.getData()
		  } else {
		    this.count = 0
		    this.renew = 0
			uni.showToast({
			    title: '结束月份不能小于开始月份',
			    mask: "true",
				icon:'none'
			});
		  }
		}
	}
  }
}
